import React, {Suspense, useEffect, useState, useContext} from 'react';
import logo from './logo.svg';
import './App.css';
import styled from 'styled-components'
import { mainStore, MainStateProvider } from './store/MainStore'
import history from './history'
import { Router } from 'react-router-dom'

import MainPage from './layouts/MainPage';
import artists from './info/artists'
import ArtistInfo from './components/ArtistInfo'
import styles from './constants';

const Container = styled.div`
  position: fixed;
  height: 100%;
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Cover = styled.div`
  position: absolute;
  z-index: 100;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background: white;
  opacity: 0.99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`

const LoadingBarContainer = styled.div`
  
  height: 20px;
  width: 50%;
  background: white;
  border-radius: 25px;
  overflow: hidden
`
function App() {

  return (
    <MainStateProvider>

        <Router history={history}>

          <AppWrapper />
        </Router>

    </MainStateProvider >
  );
}

export default App;


function AppWrapper() {
  
  const {state, dispatch} = useContext(mainStore)
  const [timerDone, setTimerDone] = useState(false)
  const [startLoading, setStartLoading] = useState(false)


  useEffect( ()  => {
    let timer1:any = null

    timer1 = setTimeout(() => {
      setStartLoading(true)
    }, 500)


    const resizeFunction = () => {
      // change width from the state object
      if (window.innerWidth >= 960) {
        dispatch({type: 'setMobileOpen', mobileOpen: false});
        console.log('mobile close')

      }
      else {
        dispatch({type: 'setMobileOpen', mobileOpen: true});
        console.log('mobile open')

      }

      let newHeight = window.innerHeight
      if(state.height !== newHeight) {
        dispatch({
          type: 'setHeight',
          height: newHeight
        })
      }
    };

    resizeFunction()

    window.addEventListener('resize', resizeFunction);

    function setBrowser(type: any) {
      dispatch({
        type: 'setBrowser',
        browser: type
      })

    }

    if((navigator.userAgent.indexOf("Opera") || navigator.userAgent.indexOf('OPR')) != -1 ) {
      setBrowser('op')
    }
    else if(navigator.userAgent.indexOf("Chrome") != -1 ) {
      setBrowser('chr')
    }
    else if(navigator.userAgent.indexOf("Safari") != -1) {
      setBrowser('saf')
    }
    else if(navigator.userAgent.indexOf("Firefox") != -1 ) {
      setBrowser('fir')
    }
    else if((navigator.userAgent.indexOf("MSIE") != -1 )) {
      setBrowser('ie')
    }  
  

    startProcessing()

    timer1 = setTimeout(() => {
      setTimerDone(true)
    }, 5000)


    return () => {
      window.removeEventListener("resize", resizeFunction);
      clearTimeout(timer1)

    }


  }, [])

  return (
    <Container>

     
      <MainPage 

      />
      

      {
        (!state.imagesLoaded || !timerDone)  && 
          <Cover>
            <img 
              src={`${state.photoUrl}art/logo.png`} 
              style={{height: '200px', marginBottom: '50px'}}
            />
            <LoadingBarContainer>
              <div 
                style={{
                  height: '100%',
                  width: startLoading ? '100%' : '0%',
                  transition: 'all 4.5s linear',
                  background: styles.red
                }}
              />
            </LoadingBarContainer>
          </Cover>
      }
      
       

    </Container>
  );

  function rgbToHex(r:number, g:number, b:number, a:number) {
    if (r > 255 || g > 255 || b > 255 || a > 255)
        throw "Invalid color component";
    return ((r << 16) | (g << 8) | b).toString(16);
  }

  function addImageProcess(src: string): any{
    return new Promise((resolve, reject) => {
      let img:HTMLImageElement = new Image()
      img.src = src
      img.crossOrigin = "anonymous";
      img.onload = () => resolve(img)
      img.onerror = reject
    })
  }

  async function processPreviews() {
    let newPreviews:any[] = []

    for(let i = 0; i < state.previewPhotos.length; i++) {
      let prop = state.previewPhotos[i]
        
      let src = prop.photo
      let imageLoaded:HTMLImageElement =  await addImageProcess(src)

      Object.assign(prop, {image: imageLoaded});
      
      newPreviews.push(prop)

      
    }

    dispatch({
      type: 'setPreviewPhotos',
      previewPhotos: newPreviews,
    })
  }

  async function processArtists() {
    let newArtists:any[] = []

    for(let i = 0; i < artists.length; i++) {
      let prop = artists[i]
        
      let src = state.photoUrl + prop.photo
      let imageLoaded:HTMLImageElement =  await addImageProcess(src)
      /*
      img.onload = () => {
        */
       

        let canvas = document.createElement('canvas');
        if(canvas) {
          let context = canvas.getContext('2d');
          if(context) {
            canvas.width = 2;
            canvas.height = 2;
            context.drawImage(imageLoaded, 0, 0 );
            var myData = context.getImageData(0, 0, 2, 2).data
            console.log(myData)
            var hex = "#" +  (rgbToHex(myData[0], myData[1], myData[2], myData[3] ) );
            prop.color = hex
            prop.photo = src
            Object.assign(prop, {image: imageLoaded});
            /*
            let found = false
            for(let i = 0; i < newArtists.length; i++) {
              if(newArtists[i].name === prop.name) {
                newArtists[i].color = hex
                found = true
              }
            }
            if(!found)
            */
            newArtists.push(prop)
          }      
        }



    }

    dispatch({
      type: 'setArtists',
      allArtists: newArtists,
    })
  }

  async function startProcessing() {
    await processArtists()


    await processPreviews()
            
    dispatch({
      type: 'setImagesLoaded',
      imagesLoaded: true
    })

  }
}

